<template>
  <div>
    <div>
      <v-alert
          v-model="alert"
          border="left"
          colored-border
          dismissible
          transition="fade-transition"
          elevation="2"
          type="warning"
      >
        belum siap untuk menyimpan, mohon tunggu <strong>5</strong> detik lagi, lalu coba kembali
      </v-alert>
    </div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row class="" no-gutters style="position: relative">
          <v-col class="d-flex justify-center" style="position: absolute"><h2 class="white--text font-weight-medium">
            Detail Kontak</h2>
          </v-col>
          <v-col
              class="d-flex justify-start" style="z-index: 1; position: relative;">
            <v-icon
                color="white"
                dense
                style="cursor:pointer; "
                x-large
                @click="backToHome"
            >mdi-chevron-left
            </v-icon>
            <h2 class="white--text" style="cursor:pointer;" @click="backToHome">Back</h2>
          </v-col>
        </v-row>

      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-container fluid>
        <v-row>
          <v-col>
            <v-card class="pa-3 mx-2 my-0 rounded-b-lg" color="#f2f8e5" outlined>
              <h4 style="color: #5c8601;;">Petunjuk Pengisian:</h4>
              <h5 class="font-weight-regular" style="color: #5c8601;">1. DITUGASKAN KEPADA dan TANGGAL DIBERIKAN
                Bagian ini akan diisi oleh Hamba Daerah/saudara yang ditugaskan untuk membagikan daerah
              </h5>
              <v-divider
                  :thickness="3"
                  class="border-opacity-50"
                  color="success"
              ></v-divider>
              <h5 class="font-weight-regular" style="color: #5c8601;">2. TANGGAL DIKEMBALIKAN
                di bagian ini saudara bisa membuat catatan mengenai daerah yang dikerjakan</h5>
              <v-divider
                  :thickness="3"
                  class="border-opacity-50"
                  color="success"
              ></v-divider>
              <h5 class="font-weight-regular" style="color: #5c8601;">3. CATATAN
                di bagian ini saudara bisa membuat catatan mengenai daerah yang dikerjakan</h5>
              <v-divider
                  :thickness="3"
                  class="border-opacity-50"
                  color="success"
              ></v-divider>
              <h5 class="font-weight-regular" style="color: #5c8601;">4. JENIS DAERAH
                Silakan pilih daerah pribadi jika saudara ingin menjadikan daerah ini sebagai daerah pribadi</h5>
              <v-divider
                  :thickness="3"
                  class="border-opacity-50"
                  color="success"
              ></v-divider>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="d-flex my-4 ml-auto justify-center" width="100%">
            <v-avatar
                color="#f5f5f5"
                size="160">
              <v-icon
                  color="black"
                  size="128"
              >mdi-account-multiple-outline
              </v-icon>
            </v-avatar>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.nomorKartuDaerah" class="d-flex my-4 ml-auto justify-center" width="100%">
            <h2>{{detailDaerah.nomorKartuDaerah}}</h2>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Ditugaskan Kepada</h5>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.ditugaskanKepada !== null">
            <DialogFilterDitugaskanKepada
                :filter-selected="filterSelectedDitugaskanKepada"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Ditugaskan Kepada 2</h5>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.ditugaskanKepada !== null">
            <DialogFilterDitugaskanKepada2
                :filter-selected="filterSelectedDitugaskanKepada2"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.tanggalDiberikan !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <h5 class="font-weight-regular grey--text text--darken-2">Tanggal diberikan</h5>
              <v-dialog
                  ref="dialog"
                  v-model="dateDialogDiberikan"
                  persistent
                  width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                      :value="formatDateDialogtanggalDiberikan"
                      background-color="#ececec"
                      class="black--text"
                      label="Masukkan Tanggal hari ini..."
                      prepend-inner-icon="mdi-calendar"
                      readonly
                      solo
                      v-bind="attrs"
                      v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                    v-model="tanggalDiberikan"
                    scrollable
                >
                  <v-spacer></v-spacer>
                  <v-btn
                      color="primary"
                      text
                      @click="dateDialogDiberikan = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                      color="primary"
                      text
                      @click="updateTanggalDiberikan(tanggalDiberikan)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.tanggalDikembalikan !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <h5 class="font-weight-regular grey--text text--darken-2">Tanggal dikembalikan</h5>
              <v-dialog
                  ref="dialog"
                  v-model="dateDialogDikembalikan"
                  persistent
                  width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                      :value="formatDateDialogtanggalDikembalikan"
                      background-color="#ececec"
                      class="black--text"
                      label="Masukkan Tanggal hari ini..."
                      prepend-inner-icon="mdi-calendar"
                      readonly
                      solo
                      v-bind="attrs"
                      v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                    v-model="tanggalDikembalikan"
                    scrollable
                >
                  <v-spacer></v-spacer>
                  <v-btn
                      color="primary"
                      text
                      @click="dateDialogDikembalikan = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                      color="primary"
                      text
                      @click="updateTanggalDikembalikan(tanggalDikembalikan)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.gambar !== null">
            <v-img :src="detailDaerah.gambar"></v-img>
          </v-col>
        </v-row>
        <v-col v-if="detailDaerah && detailDaerah.linkPeta !== null">
        <v-btn
            :href="detailDaerah.linkPeta"
            target="_blank"
            class="mr-2 my-2"
            color="#7fb901"
            elevation="2"
            width="100%"
        ><h3 class="white--text">Silakan klik di sini untuk melihat peta</h3></v-btn>
        </v-col>        
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.catatan !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <div>
                <h5 class="mb-2 font-weight-regular grey--text text--darken-2">Catatan</h5>
                <v-textarea
                    v-model="detailDaerah.catatan"
                    background-color="#ececec"
                    class="black--text"
                    label="silakan catat di sini informasi apapun mengenai daerah ini setelah saudara/i mengerjakannya"
                    outlined
                    solo
                    @change="updateCatatan(detailDaerah.catatan)"
                ></v-textarea>
              </div>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Jenis Daerah</h5>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.jenisDaerah !== null">
            <DialogFilterJenisDaerah
                :filter-selected="filterSelectedJenisDaerah"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailDaerah && detailDaerah.tempatPDL">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <div>
                <h5 class="font-weight-regular grey--text text--darken-2">Tempat PDL</h5>
                <h5 class="font-weight-medium black--text">{{detailDaerah.tempatPDL}}</h5>
              </div>
            </v-card>
          </v-col>
        </v-row>
     

      </v-container>

      <div style="height: 50px"></div>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import DialogFilterDitugaskanKepada from "@/views/DetailDaerah/DialogFilterDitugaskanKepada";
import DialogFilterDitugaskanKepada2 from "@/views/DetailDaerah/DialogFilterDitugaskanKepada2";
import DialogFilterJenisDaerah from "@/views/DetailDaerah/DialogFilterJenisDaerah";
import {mapGetters} from "vuex";

import { use } from 'echarts/core';
import { CanvasRenderer } from 'echarts/renderers';
import { BarChart, LineChart } from 'echarts/charts';
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  ToolboxComponent
} from 'echarts/components';
import store from "@/store";

use([
  CanvasRenderer,
  BarChart,
  LineChart,
  
  TitleComponent,
  TooltipComponent,
  ToolboxComponent,
  LegendComponent,
  GridComponent
]);

export default{
  name: "Home",
   data() {
    return {

      alert: false,
      listDataDaerah: [],
      search: "",
      emptyList: [],
      dateDialogDiberikan: false,
      dateDialogDikembalikan: false,
      tanggalDiberikan: null,
      tanggalDikembalikan: null,
    }
  },
  computed: {
    ...mapGetters({
      lastTelepon: 'base/lastTelepon',
      lastSurat: 'base/lastSurat',
      listPenyiar: 'base/listPenyiar',
      listPenyiarDetailDaerahDitugaskanKepada: 'base/listPenyiarDetailDaerahDitugaskanKepada',
      listPenyiarDetailDaerahDitugaskanKepada2: 'base/listPenyiarDetailDaerahDitugaskanKepada2',
      listDaerah: 'base/listDaerah',
      titleTeleponSurat: 'base/titleTeleponSurat',
      listJenisTanggapan: `base/listJenisTanggapan`,
      listChartData: `base/listChartData`,
      detailDaerah: `base/detailDaerah`,
      saveReady: `base/saveReady`,

    }),
    formatDateDialogtanggalDiberikan() {
      moment.locale('id')
      if (this.detailDaerah.tanggalDiberikan) {
        if (this.detailDaerah.tanggalDiberikan !== "-") {
          return moment(this.detailDaerah.tanggalDiberikan).hour(0).minute(0).second(0).format('DD MMMM YYYY')
        }
      }
      return ""
    },
    formatDateDialogtanggalDikembalikan() {
      moment.locale('id')
      if (this.detailDaerah.tanggalDikembalikan) {
        if (this.detailDaerah.tanggalDikembalikan !== "-") {
          return moment(this.detailDaerah.tanggalDikembalikan).hour(0).minute(0).second(0).format('DD MMMM YYYY')
        }
      }
      return ""
    },
  },
  // watch: {
  //     listPenyiar() {
  //       console.log("gk kelamaan apa ya")
  //       this.filterData();
  //   }
  // },
  methods:
  
      {
        backToHome() {
          this.$router.push('/')
        },
        filterData() {
          let loadDataDaerah = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadDataDaerah) {
            let penyiarTerpilih = this.listPenyiar ? this.listPenyiar.filter(x => x.isChecked).map(x => x.name) : [];
            let jenisTanggapanTerpilih = this.listJenisTanggapan ? this.listJenisTanggapan.filter(x => x.isChecked).map(x => x.name) : [];
            let loadDataDaerahPenyiar = [];
            let loadDataDaerahTanggapan = [];
            this.storageSave("base", "listDaerah", loadDataDaerah.slice(0, 100));
            if (this.search) {
              loadDataDaerah = loadDataDaerah.filter(x => x.namaPemilikusaha.toLowerCase().includes(this.search.toLowerCase()));
            }
            if (penyiarTerpilih.length > 0)
              penyiarTerpilih.forEach(x => {
                loadDataDaerahPenyiar = [...new Set([...loadDataDaerahPenyiar, ...loadDataDaerah.filter(xx => xx.name === x)])];
              });
            else loadDataDaerahPenyiar = loadDataDaerah

            if (jenisTanggapanTerpilih.length > 0) {
              jenisTanggapanTerpilih.forEach(x => {
                loadDataDaerahTanggapan = [...new Set([...loadDataDaerahTanggapan, ...loadDataDaerahPenyiar.filter(xx => xx.jenisTanggapan === x)])];
              });
            } else loadDataDaerahTanggapan = loadDataDaerahPenyiar
            loadDataDaerahTanggapan = loadDataDaerahTanggapan.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            })

            this.$store.commit("base/listDaerah", loadDataDaerahTanggapan.slice(0, 100));
          }
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
        saveNotReady(){
          if (!this.saveReady) {
            this.dateDialog = false;
            this.alert = true;
            window.setInterval(() => {
              this.alert = false;
            }, 5000)
            return true;
          }
          return false;
        },
        async updateTanggalDiberikan(selected) {
          if(this.saveNotReady()) return;
          moment.locale('id')
          this.tanggalDiberikan = selected
          this.dateDialogDiberikan = false;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 4);
                console.log(updateCell.effectiveFormat)
                updateCell.numberFormat = {
                  type: "DATE",
                  pattern: "yyy-mm-ddThh:mm:ss.000Z",
                }
                updateCell.value = moment(this.tanggalDiberikan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                await sheet.saveUpdatedCells();
                this.detailDaerah.tanggalDiberikan = moment(this.tanggalDiberikan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },
        async updateTanggalDikembalikan(selected) {
          if(this.saveNotReady()) return;
          moment.locale('id')
          this.tanggalDikembalikan = selected
          this.dateDialogDikembalikan = false;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 5);
                console.log(updateCell.effectiveFormat)
                updateCell.numberFormat = {
                  type: "DATE",
                  pattern: "yyy-mm-ddThh:mm:ss.000Z",
                }
                updateCell.value = moment(this.tanggalDikembalikan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                await sheet.saveUpdatedCells();
                this.detailDaerah.tanggalDikembalikan = moment(this.tanggalDikembalikan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },
        async updateCatatan(selected) {
          if(this.saveNotReady()) return;
          this.detailDaerah.catatan = selected;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 10);
                updateCell.value = selected;
                await sheet.saveUpdatedCells();
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },

        async filterSelectedDitugaskanKepada(selected) {
          if(this.saveNotReady()) return;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 1);
                updateCell.value = selected.name;
                await sheet.saveUpdatedCells();
                this.detailDaerah.ditugaskanKepada = selected.name;
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },
        async filterSelectedDitugaskanKepada2(selected) {
          if(this.saveNotReady()) return;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 3);
                updateCell.value = selected.name;
                await sheet.saveUpdatedCells();
                this.detailDaerah.ditugaskanKepada2 = selected.name;
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },
        async filterSelectedJenisDaerah(selected) {
          if(this.saveNotReady()) return;
          let loadData = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadData) {
            let chosen = loadData.filter(x => x.key === this.detailDaerah.key);
            if (chosen) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Daerah Fisik"];
                let updateCell = sheet.getCell(this.detailDaerah.key, 8);
                updateCell.value = selected.name;
                await sheet.saveUpdatedCells();
                this.detailDaerah.jenisDaerah = selected.name;
                this.storageSave("base", "detailDaerah", this.detailDaerah);
                this.vuexFromStorage("base", "detailDaerah");
                console.log("Berhasil update")
              }
            }
          }
        },
      },
  async mounted() {
    moment.locale('id');
    this.filterData();
    if (!this.detailDaerah) await this.$router.push('/');
    console.log(this.detailDaerah);

  },


  components: {DialogFilterDitugaskanKepada, DialogFilterDitugaskanKepada2, DialogFilterJenisDaerah}
};
</script>
<style scoped>
html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}

.v-text-field--outlined >>> fieldset {
  border-color: #7FB901;
}
.chart {
  height: 50vh;
}
</style>
