<template>
  <div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row class="py-4" no-gutters style="position: relative">
          <v-col class="d-flex justify-center" style="position: absolute"><h2 class="white--text">
              DAERAH
            </h2></v-col>
          <v-col
              class="d-flex justify-space-between" style="z-index: 1; position: relative;">
            <v-icon
                color="white"
                dense
                style="cursor:pointer; "
                x-large
                @click="backToHome"
            >mdi-chevron-left
            </v-icon>
            <h2 class="white--text" style="cursor:pointer;" @click="backToHome">Back</h2>

          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col>
            <v-text-field v-model="search" background-color="6c9d00" class="rounded-l text--white mainSearch" clearable
                          dense
                          filled label="Search"
                          solo-inverted
                          @input="filterData()" @click:clear="filterData">
              <template v-slot:prepend-inner="">
                <v-icon color="white">mdi-magnify</v-icon>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-card v-for="daerah in listDaerah" :key="daerah.key" flat>
        <v-container v-if="daerah && daerah.ditugaskanKepada" fluid>
          <v-row @click="gotoDetailDaerah(daerah)" style="cursor: pointer;">
            <v-avatar size="80" class="ml-2 mb-6">
              <v-img :src="daerah.gambar"></v-img>
            </v-avatar>
            <v-col>
              <v-row>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-medium">
                  {{ daerah.ditugaskanKepada }}</h4></v-col>
              </v-row>
              <div v-if="daerah.ditugaskanKepada2">
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-regular">
                  {{ daerah.ditugaskanKepada2 }}</h4></v-col>
              </v-row>
              </div>
            </v-col>
            <v-col class="ml-auto mr-2" style="text-align: end">
              <v-icon>mdi-chevron-right</v-icon>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-container>
      </v-card>
      <div style="height: 50px"></div>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import {mapGetters} from "vuex";

export default {
  name: "ListDaerah",
  data() {
    return {
      listDataDaerah: [],
      search: "",
    }
  },
  computed: {
    ...mapGetters({
      lastTelepon: 'base/lastTelepon',
      lastSurat: 'base/lastSurat',
      listPenyiar: 'base/listPenyiar',
      listDaerah: 'base/listDaerah',
      titleTeleponSurat: 'base/titleTeleponSurat',
      listJenisTanggapan: `base/listJenisTanggapan`,

    })
  },
  // watch: {
  //     listPenyiar() {
  //       console.log("gk kelamaan apa ya")
  //       this.filterData();
  //   }
  // },
  methods:
      {
        gotoDetailDaerah(daerah) {
          this.$store.commit("base/detailDaerah", daerah);
          this.$router.push('/detail-daerah')
        },
        backToHome() {
          this.$router.push('/')
        },
        filterData() {
          let loadDataDaerah = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadDataDaerah) {
            let loadDataDaerahPenyiar = [];
            let loadDataDaerahTanggapan = [];
            this.storageSave("listDaerah", loadDataDaerah.slice(0, 100));
            if (this.search) {
              loadDataDaerah = loadDataDaerah.filter(x => x.ditugaskanKepada.toLowerCase().includes(this.search.toLowerCase()));
            }
            loadDataDaerahPenyiar = loadDataDaerah
         loadDataDaerahTanggapan = loadDataDaerahPenyiar
            loadDataDaerahTanggapan = loadDataDaerahTanggapan.filter(x => x.tanggalDiberikan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDiberikan) - new Date(a.tanggalDiberikan);
            })
            
            this.$store.commit("base/listDaerah", loadDataDaerahTanggapan.slice(0, 100));
          }
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
      },
  async mounted() {
    moment.locale('id');
      this.filterData();
  },
  
  components: {}
};
</script>
<style scoped>
html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}
</style>
