import Vue from "vue";
import VueRouter from "vue-router";
import AppLayout from "@src/layouts/AppLayout.vue";
import Home from "@src/views/Home/Home.vue";
import Daerah from "@src/views/Daerah/Daerah.vue";
import ListTeleponSurat from "@/views/ListTeleponSurat/ListTeleponSurat";
import ListDaerah from "@/views/ListDaerah/ListDaerah.vue";
import DetailKontak from "@/views/DetailKontak/DetailKontak";
import DetailDaerah from "@/views/DetailDaerah/DetailDaerah";


Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: AppLayout,
        children: [
            {
                path: "",
                component: Home,
            },
            {
                path: "daerah",
                component: Daerah,
            },
            {
                path: "list-daerah",
                component: ListDaerah,
            },
            {
                path: "list-telepon-surat",
                component: ListTeleponSurat,
            },
            {
                path: "detail-kontak",
                component: DetailKontak,
            },
            {
                path: "detail-daerah",
                component: DetailDaerah,
            },
        ],
    },

];

const router = new VueRouter({
    routes,
});

export default router;
