<template>
  <div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row no-gutters>
          <v-col class="d-flex">
            <DialogFilter
                :filter-all="filterAll"
                :filter-selected="filterSelected"
            />
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-col><h1 class="white--text">Daerah Fisik</h1></v-col>
        </v-row>
        <v-row no-gutters>
          <v-col>
            <v-text-field v-model="search" background-color="6c9d00" class="rounded-l text--white mainSearch" clearable
                          dense
                          filled label="Search"
                          solo-inverted
                          @input="filterDitugaskanKepada()" @click:clear="filterDitugaskanKepada">
              <template v-slot:prepend-inner="">
                <v-icon color="white">mdi-magnify</v-icon>
              </template>
            </v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-card>
        <v-container v-if="!listDataDaerah"><h1>Sedang mengunduh data untuk pertama kali, mohon menunggu...</h1>
        </v-container>
        <v-container v-if="lastDaerah && lastDaerah.nomorKartuDaerah" fluid>
          <v-row>
            <v-col class="d-flex"><h3 class="text--darken-1">{{ lastDaerah.tempatPDL }}</h3>
              <h3 class="ml-auto green--text text--darken-1" style="cursor:pointer"
                  @click="goToDaerah">See All
              </h3>

            </v-col>
          </v-row>
          <v-row style="cursor: pointer;" @click="gotoDetailDaerah(lastDaerah)">
              <v-avatar size="80" class="ml-2 mb-6">
                <v-img :src="lastDaerah.gambar"></v-img>
              </v-avatar>
            <v-col>
              <v-row>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-medium">
                  {{ lastDaerah.nomorKartuDaerah }}</h4></v-col>
              </v-row>
              <v-row no-gutters>
                <v-col class="d-flex"><h4 class="text--lighten-1 font-weight-regular">
                  {{ lastDaerah.ditugaskanKepada }}</h4></v-col>
              </v-row>
            </v-col>
            <v-col class="ml-auto mr-2" style="text-align: end">
              <v-icon>mdi-chevron-right</v-icon>
            </v-col>
          </v-row>
          <v-divider></v-divider>
        </v-container>

      </v-card>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import {mapGetters} from "vuex";
import DialogFilter from "@/views/Daerah/DialogFilter";

export default {
  name: "HomeDaerah",
  data() {
    return {
      allChecked: true,
      search: "",
    }
  },
  computed: {
    ...mapGetters({
      lastDaerah: 'base/lastDaerah',
      listDataDaerah: 'base/listDataDaerah',
      listTugasDaerah: 'base/listTugasDaerah',
    })
  },
  watch: {
    // lastDaerah(value) {
    //   console.log("lastDaerah")
    //   console.log(value)
    // }
  },
  methods:
      {
        gotoDetailDaerah(daerah) {
          this.$store.commit("base/detailDaerah", daerah);
          this.$router.push('/detail-daerah')
        },
        goToDaerah() {
          this.$router.push('/list-daerah')
        },
        filterSelected() {
          let loadDataDaerah = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadDataDaerah) {
            let penyiarTerpilih = this.listTugasDaerah.filter(x => x.isChecked).map(x => x.name);
            let loadDataTugasDaerah = [];
            if (this.search) {
              loadDataDaerah = loadDataDaerah.filter(x => x.ditugaskanKepada.includes(this.search));
            }
            penyiarTerpilih.forEach(x => {
              loadDataTugasDaerah = [...new Set([...loadDataTugasDaerah, ...loadDataDaerah.filter(xx => xx.ditugaskanKepada === x)])];
            });
            let loadDataDaerahBelumDitugaskan = loadDataTugasDaerah.filter(x => x.tanggalDiberikan === "-");
            loadDataTugasDaerah = loadDataTugasDaerah.filter(x => x.tanggalDiberikan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDiberikan) - new Date(a.tanggalDiberikan);
            })
            loadDataTugasDaerah = [...new Set([...loadDataTugasDaerah, ...loadDataDaerahBelumDitugaskan])];
            console.log(loadDataTugasDaerah)

            this.$store.commit("base/lastDaerah", loadDataTugasDaerah.shift() ?? {});
          }
        },
        filterAll() {
          let loadDataDaerah = mixin.methods.storageLoad("base","listDataDaerah");
          if (loadDataDaerah) {
            if (this.search) {
              loadDataDaerah = loadDataDaerah.filter(x => x.ditugaskanKepada.toLowerCase().includes(this.search.toLowerCase()));
            }
            let loadDataDaerahBelumDitugaskan = loadDataDaerah.filter(x => x.tanggalDiberikan === "-");
            loadDataDaerah = loadDataDaerah.filter(x => x.tanggalDiberikan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDiberikan) - new Date(a.tanggalDiberikan);
            })
            loadDataDaerah = [...new Set([...loadDataDaerah, ...loadDataDaerahBelumDitugaskan])];

            this.$store.commit("base/lastDaerah", loadDataDaerah.shift() ?? {});
          }
        },
        filterDitugaskanKepada() {
          this.$store.commit("base/searchDaerah", this.search);
          if (this.allChecked) this.filterAll();
          else this.filterSelected();
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
      },
  async mounted() {
    moment.locale('id');
  },
  components: {DialogFilter}
};
</script>
<style scoped>
html {
  overflow: scroll;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent; /* make scrollbar transparent */
}
</style>
